.container {
  max-width: 1140px;
  margin: 0 auto;
}

.flow-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flow-content > * + * {
  margin: var(--spacer, 1.5rem) 0 0 0;
}

.split {
  display: flex;
  flex-direction: column;
}

.split > * {
  flex-basis: 100%;
}

.split > * + * {
  margin: var(--space, 2rem) 0 0 0;
}

@media screen and (min-width: 40em) {
  .split {
    flex-direction: row;
  }
  
  .split > * + * {
    margin: 0 0 0 var(--space, 2rem);
  }
}

.small-M {
  margin-top: .8rem;
}

.medium-M {
  margin-top: 1rem;
}

.large-M {
  margin-top: 2rem;
}

.x-large-M {
  margin-top: 3rem;
}

.xx-large-M {
  margin-top: 4rem;
}