.section-subHeading {
  max-width: 60ch;
}

.section-features {
  background-color: var(--clr-neutral-300);
  text-align: center;
  padding-left: 2rem;
  padding-right: 2rem;

  & .split {
    --spacer: 1.8rem;
  }

  &__card > * + * {
    margin-top: 2rem;
  }
  
  @media screen and (min-width: 40em) {
    & {
      text-align: left;
    }

    & .flow-content {
      align-items: flex-start;
    }

    &__card > * + * {
      margin-top: 0;
    }
  }

}
