.primary-header {
  padding: 2rem 1rem;
  background-color: var(--clr-neutral-100);
  position: fixed;
  width: 100vw;
  z-index: 20;
  box-shadow: 0.2rem 0.2rem 0.5rem rgba(#000, 0.2);

  /* links */
  &__nav {
    position: absolute;
    top: 6.5rem;
    display: flex;
    justify-content: center;
    border-radius: 5px;
    text-align: center;
    width: 88%;
    background-color: var(--clr-neutral-100);
    box-shadow: 0 0.5rem 4rem rgba(0, 0, 0, 0.8);
    transition: all 0.5s;

    &__list {
      padding: 0.5rem;
    }

    &__link {
      color: var(--clr-secondary);
      font-weight: 400;
    }
  }

  @media screen and (min-width: 40em) {
    & .navigation {
      display: none;
    }

    & {
      padding: 0 2rem;
    }

    &__inner {
      display: flex;
      align-items: baseline;
    }

    &__inner .cta {
      min-width: 10rem;
      padding: .5rem 1rem;
      font-size: .9rem;
    }

    &__logo {
      min-width: 7rem;
      align-self: center;
    }

    &__nav {
      position: relative;
      top: 0;
      width: 100%;
      background-color: transparent;
      box-shadow: initial;
    }

    &__nav__box {
      display: flex;
    }

    &__nav__link {
      font-size: .85rem;
      font-weight: 300;
    }
  }
}

/* navigation hambuger */
.navigation {
  position: relative;
  transition: all 0.5s;

  &-hamburger {
    position: absolute;
    right: 1.5rem;
    top: -0.9rem;
  }
}

.hidden {
  visibility: hidden;
  opacity: 0;
  transform: translateY(3rem);
  transition: all 0.5s;
}

.show {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
  transition: all 0.5s;
}
