.hero {
  padding: 0 0 3rem 0;
  top: -4rem;
  background-color: var(--clr-neutral-100);
  text-align: center;
  overflow: hidden;

  &__banner-img {
    position: relative;
    top: -1.5rem;
    z-index: 2;
  }

  &__content {
    padding: 0 2rem;
  }

  &__svg img {
    width: 40rem;
    position: absolute;
    top: 5rem;
    z-index: 1;
  }

  @media screen and (min-width: 40em) {
    & {
      padding: 9rem 0 5rem;
    }
    
    &__banner {
      order: 2;
    }
    
    &__banner-img {
      position: absolute;
      top: 22rem;
      max-width: 40rem;
    }
    
    &__banner-mockup {
      transform: translate(8rem, -20rem);
      min-width: 36rem;
    }
    
    &__content {
      padding: 0;
    }
    
    & .flow-content {
      display: initial;
      padding-left: -1rem;
    }
    
    &__content {
      align-self: center;
      text-align: left;
      
      & .page-title {
        width: 15ch;
      }
      
      & .page-intro {
        width: 34ch;
      }
    }

    &__svg img {
      position: relative;
      top: -5rem;
      right: -16rem;
      transform: scale(2);
    }
  }
}
