*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4 {
  line-height: 1;
}

body,
h1,
h2,
h3,
h4,
p,
dl,
dd,
figure {
  margin: 0;
}

ul[class],
ol[class] {
  list-style: none;
  padding: 0;
}

input,
button,
textarea,
select {
  font: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

body {
  font-family: var(--ff-publicSans);
  Font-size: 18px;
  font-weight: 400;
  line-height: 1.6;
  scroll-behavior: smooth;
  background-color: var(--clr-neutral-100);
  
}

.wrapper {
  overflow: hidden;
  position: relative;
}