.footer {
  background-color: var(--clr-secondary);
  text-align: center;
  padding-left: 1rem;
  padding-right: 1rem;

  &-left {
    --spacer: 1rem;
  }

  &-links a {
    color: var(--clr-neutral-100);
    opacity: .7;
  }

  &-logo > img {
    margin: 0 auto;

    background-color: aliceblue;
    padding: 1rem .8rem;
    border-radius: 5px;
  }

  &-icons {
    width: 50%;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 2.5rem;
  }

  &-link img {
    width: 1.8rem;
  }

  & .copyright {
    color: var(--clr-neutral-100);
    opacity: .4;
  }

  @media screen and (min-width: 40em) {
    & .flex {
      display: flex;
    }

    &-left {
      display: flex;
      min-width: 50%;
    }

    @media screen and (max-width: 50rem) {
      &-left {
        flex-direction: column;
      }
    }

    &-right {
      min-width: 20%;
      margin-left: auto;
    }

    &-left * {
      flex-basis: 100%;
    }

    &-left .flow-content {
      margin: 0;
    }

    &-logo {
      min-width: 50%;
    }

    &-icons {
      width: 100%;
      margin-left: 1rem;
    }

    &-links {
      min-width: 40%;
    }
  }
}
