.articles {
  --spacer: .8rem;
  background-color: var(--clr-neutral-200);
  padding-left: 2.2rem;
  padding-right: 2.2rem;

  & header {
    text-align: center;
  }
}

.article .flow-content {
  align-items: flex-start;
}

.article {
  background-color: var(--clr-neutral-100);
  border-radius: 5px;
  overflow: hidden;

  & .flow-content {
    padding: 2rem 1.5rem 3rem;
  }

  & img {
    width: 100%;
  }
  
  @media screen and (min-width: 40em) {
    & .flow-content {
      padding: 1.5rem;
    }

    & img {
      height: 12rem;
    }
  }
}

.author {
  display: block;
  order: -1;
}